import { useMemo } from "react";
import { BigNumber } from "@ethersproject/bignumber";

import theme from "../../theme";

import DataField from "../DataField";

import useCore from "../../hooks/useCore";
import { IProjectIdProp, ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalance } from "../../utils/formatBalance";
import useGetTokenSalePrice from "../../hooks/state/useGetTokenSalePrice";
import useGetMinAndMaxAllocation from "../../hooks/state/useGetMinAndMaxAllocation";
import useConvertUSDtoBNB from "../../hooks/state/useConvertUSDtoBNB";

const MinAndMaxAllocation = (props: ITokenSymbolProp & IProjectIdProp) => {
  const { tokenSymbol, id } = props;

  const core = useCore();
  const tradeValue = useGetTokenSalePrice(tokenSymbol, id);
  const minAndMaxAllocation = useGetMinAndMaxAllocation(tokenSymbol, id);

  const minAllocationFormatted = useMemo(() => {
    const value = minAndMaxAllocation.minAllocation
      .mul(tradeValue.value)
      .div(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal));

    return Number(getDisplayBalance(value, 18, 3)).toLocaleString(
      'en-US',
      { minimumFractionDigits: 2, maximumFractionDigits: 2 }
    );
  }, [minAndMaxAllocation, core, tradeValue, tokenSymbol]);

  const maxAllocationFormatted = useMemo(() => {
    const value = minAndMaxAllocation.maxAllocation
      .mul(tradeValue.value)
      .div(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal));

    return Number(getDisplayBalance(value, 18, 3)).toLocaleString(
      'en-US',
      { minimumFractionDigits: 2, maximumFractionDigits: 2 }
    );
  }, [minAndMaxAllocation, core, tradeValue, tokenSymbol]);

  const maxAllocation = useMemo(() => {
    if (id.toLocaleLowerCase() === 'scallop') return '$300';
    return '$750'
  }, [id]);

  const bnbValue = useConvertUSDtoBNB(props.id.toLowerCase() === 'scallop' ? "300" : "750")

  return (
    <div className="m-b-16">
      <DataField
        label={"Max. Allocation"}
        value={maxAllocation}
        isValueLoading={false}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
      />
      <DataField
        value={`~${Number(bnbValue.value).toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })} BNB`}
        isValueLoading={bnbValue.isLoading}
        valueFontSize={12}
        valueFontWeight={600}
        valueFontColor={theme.color.transparent[100]}
      />
    </div>
  );
}

export default MinAndMaxAllocation;
