import { useMemo } from "react";

import theme from "../../theme";

import DataField from "../DataField";

import { IProjectIdProp, ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalance } from "../../utils/formatBalance";

import useGetTotalRaiseCost from "../../hooks/state/useGetTotalRaiseCost";
import useConvertUSDtoBNB from "../../hooks/state/useConvertUSDtoBNB";

const TotalRaised = (props: ITokenSymbolProp & IProjectIdProp) => {
  const totalRaiseCost = useGetTotalRaiseCost(props.tokenSymbol, props.id);

  const formattedValue: string = useMemo(() => {
    return Number(getDisplayBalance(totalRaiseCost.value, 18, 6))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [totalRaiseCost]);

  const data = useMemo(() => {
    if (props.id.toLowerCase() === 'scallop') return '$140k'
    return '$72k';
  }, [props]);

  const bnbValue = useConvertUSDtoBNB(props.id.toLowerCase() === 'scallop' ? "140000" : "72000")

  return (
    <div className="m-b-16">
      <DataField
        label={"Target raise"}
        isValueLoading={false}
        value={data}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
      />
      <DataField
        value={`~${Number(bnbValue.value).toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })} BNB`}
        isValueLoading={bnbValue.isLoading}
        valueFontSize={12}
        valueFontWeight={600}
        valueFontColor={theme.color.transparent[100]}
      />
    </div>
  );
}

export default TotalRaised;
