import { useMemo } from "react";

import theme from "../../theme";

import DataField from "../DataField";
import TextWrapper from "../TextWrapper";

import { IProjectIdProp, ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalance } from "../../utils/formatBalance";
import useGetTokenSalePrice from "../../hooks/state/useGetTokenSalePrice";
import useConvertUSDtoBNB from "../../hooks/state/useConvertUSDtoBNB";

interface IProps {
  isDataField?: boolean;
}

const TokenPrice = (props: ITokenSymbolProp & IProps & IProjectIdProp) => {
  const { isDataField = false, tokenSymbol, id } = props;

  const tradeValue = useGetTokenSalePrice(tokenSymbol, id);

  const tokenPriceInBNBFormatted = useMemo(() => {
    return Number(getDisplayBalance(tradeValue.value, 18, 6))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 6 });
  }, [tradeValue]);

  const bnbValue = useConvertUSDtoBNB("0.15")

  if (isDataField) return (
    <div className="m-b-16">
      <DataField
        label={`${tokenSymbol.toUpperCase()} Price`}
        value={"$0.15"}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
      />
      <DataField
        value={`~${Number(bnbValue.value).toLocaleString('en-US', { minimumFractionDigits: 6, maximumFractionDigits: 6 })} BNB`}
        isValueLoading={bnbValue.isLoading}
        valueFontSize={12}
        valueFontWeight={600}
        valueFontColor={theme.color.transparent[100]}
      />
    </div>
  );

  return (
    <div>
      <TextWrapper
        text={
          `1 SCLP = $0.15`
        }
        fontSize={12}
        fontWeight={600}
        Fcolor={theme.color.transparent[100]}
      />
    </div>
  );
}

export default TokenPrice;
